import { FC } from 'react';
import FocusLock from 'react-focus-lock';

import {
  InnerWrapper,
  Wrapper,
  Title,
} from './SuccessConfirmationModal.styles';
import SuccessConfirmationModalProps from './SuccessConfirmationModal.types';

const SuccessConfirmationModal: FC<SuccessConfirmationModalProps> = ({
  isHidden,
  title,
  children,
  ...rest
}) => {
  return (
    <Wrapper {...{ ...rest, isHidden }}>
      <InnerWrapper>
        <FocusLock disabled={isHidden}>
          <Title>{title}</Title>
          {children}
        </FocusLock>
      </InnerWrapper>
    </Wrapper>
  );
};

export default SuccessConfirmationModal;
