import { styled } from 'styled-components';

import { CLASS_OVERLAY_OPEN } from '#components/Overlay';

export const Wrapper = styled.div`
  --trans: 300ms ease-in-out;
  position: fixed;
  top: 0;
  left: 0;
  z-index: ${(props) => props.theme.zIndex.modal};
  width: 100vw;
  height: 100vh;
  opacity: 0;

  /* this will cover background overlay where any click needs to close it */
  pointer-events: none;
  transform: translateY(-24px);
  transform-origin: top center;
  transition:
    transform var(--trans),
    opacity var(--trans);

  &.${CLASS_OVERLAY_OPEN} {
    opacity: 1;
    transform: translateY(0);
    transition-delay: 150ms;

    /* this will re-enable interactivity for any content within */
    & > :first-child {
      pointer-events: auto;
    }
  }
`;
