import React, { useRef, useEffect } from 'react';

import Modal from '../Base';

import { PageSpecificProps } from './ModalPageSpecific.types';

// This version of the default Modal component doesn't care about other modals on the page
// It's not controlled by global event listeners in PageState.js - hence why that logic is setup below
const ModalPageSpecific: React.FC<PageSpecificProps> = ({
  children,
  isHidden,
  onEscapeKeyPress,
  ...rest
}) => {
  // store on never changing refs to avoid adding as useEffect dependency
  const isModalHiddenRef = useRef<boolean>();
  const onEscapeKeyPressRef = useRef<typeof onEscapeKeyPress | null>(null);

  isModalHiddenRef.current = isHidden;
  onEscapeKeyPressRef.current = onEscapeKeyPress;

  useEffect(() => {
    const evtHandler = (evt: KeyboardEvent) => {
      if (evt.key === 'Escape' && !isModalHiddenRef.current) {
        onEscapeKeyPressRef.current?.();
      }
    };

    document.addEventListener('keydown', evtHandler);
    return () => document.removeEventListener('keydown', evtHandler);
  }, []);

  return <Modal {...{ ...rest, isHidden }}>{children}</Modal>;
};

export default ModalPageSpecific;
